.content {

}

.search {
    display: flex;
    justify-content: center;
    width: 281px;
    border: 1px solid rgba(0, 0, 0, 0.26);
    min-height: 49px;
}

input {
    border: none;
    outline: none;
    -webkit-appearance: none;
    width: 195px;
    font-size: 24px;
}

button {
    background-color: transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;
}

.searchList{
    overflow: scroll;
    max-height: 500px;
}